<template>
    <g-modal-page
        title="プロフィール編集"
        action-text="保存"
        @on-click-action="submit"
    >
        <t-input-group
            label="氏名"
            :status="formState('name').status"
            :feedback="formState('name').message"
        >
            <t-input type="text" v-model="form.name" label="氏名" />
        </t-input-group>
        <t-input-group
            label="氏名（かな）"
            :status="formState('nameKana').status"
            :feedback="formState('nameKana').message"
        >
            <t-input type="text" v-model="form.nameKana" label="氏名（かな）" />
        </t-input-group>
        <t-input-group
            label="Facebook URL"
            :status="formState('facebookUrl').status"
            :feedback="formState('facebookUrl').message"
        >
            <t-input
                type="text"
                v-model="form.facebookUrl"
                label="Facebook URL"
            />
        </t-input-group>
        <t-input-group
            label="Twitter URL"
            :status="formState('twitterUrl').status"
            :feedback="formState('twitterUrl').message"
        >
            <t-input
                type="text"
                v-model="form.twitterUrl"
                label="Twitter URL"
            />
        </t-input-group>
        <t-input-group
            label="Instagram URL"
            :status="formState('instagramUrl').status"
            :feedback="formState('instagramUrl').message"
        >
            <t-input
                type="text"
                v-model="form.instagramUrl"
                label="Instagram URL"
            />
        </t-input-group>
        <t-input-group
            label="Website URL"
            :status="formState('websiteUrl').status"
            :feedback="formState('websiteUrl').message"
        >
            <t-input
                type="text"
                v-model="form.websiteUrl"
                label="Website URL"
            />
        </t-input-group>
    </g-modal-page>
</template>

<script>
import GModalPage from "@/components/GModalPage.vue";
export default {
    components: {
        GModalPage
    },
    data() {
        return {
            form: {
                profile: {
                    name: null,
                    nameKana: null,
                    facebookUrl: null,
                    twitterUrl: null,
                    instagramUrl: null,
                    websiteUrl: null
                }
            },
            errors: {}
        };
    },
    methods: {
        formState(name) {
            return this.errors &&
                this.errors[name] &&
                0 < this.errors[name].length
                ? { status: "error", message: this.errors[name][0] }
                : { status: null, message: null };
        },
        submit() {}
    }
};
</script>
